export default {
    name: "BookCase",
    data() {
        return {
            modalName: '',
            listTouchStart: 0,
            listTouchDirection: null,
            // 假数据
            result: [
                {
                    coverUrl: 'url(https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg)',
                    title: '书名',
                    author: '作者',
                    updateTime: '最近一天',
                    latestChapter: '测试数据'
                }
            ]
        }
    },// todo 假数据，待删除
    created() {
        for (let i = 1; i < 20; i++) {
            let temp = Object.assign({}, this.result[0]);
            temp.title = temp.title + i;
            temp.author = temp.author + i;
            temp.updateTime = temp.updateTime + i;
            temp.latestChapter = temp.latestChapter + i;
            this.result[i] = temp;
        }
    },
    methods: {
        // ListTouch触摸开始
        touchStart(e) {
            this.listTouchStart = e.touches[0].pageX
        },
        // ListTouch计算方向
        touchMove(e) {
            this.listTouchDirection = e.touches[0].pageX - this.listTouchStart > 0 ? 'right' : 'left'
        },
        // ListTouch计算滚动
        touchEnd(e) {
            if (this.listTouchDirection === 'left') {
                this.modalName = e.currentTarget.dataset.target
            } else {
                this.modalName = null
            }
            this.listTouchDirection = null
        },
        navChange() {
            debugger;
            this.$emit('navChange', 'classify');
        }
    }
}
